.stage-group__wrapper {
	padding: 16px 20px 0 20px;
	border-radius: 16px;
	border: 1px solid var(--palette-live-grey-6);
	margin: 0 0 20px 0;

	&:last-child {
		margin: 0 0 0 0;
	}

	.stage-group__headers {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 0 16px 0;
		border: solid var(--palette-live-grey-6);
		border-top-width: 0;
		border-bottom-width: 1px;
		border-left-width: 0;
		border-right-width: 0;

		.stage-group__participants {
			color: var(--palette-live-grey-0);
			font-size: 18px;
			margin: 0 0 0 0;
			font-weight: 700;
		}

		.stage-group__group-container {
			display: flex;
			align-items: center;

			.struc-new__icon {
				width: 25px;
				height: 25px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}

			.round-robin__icon {
				width: 25px;
				height: 25px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}

			.stage-group__identify {
				color: var(--palette-live-grey-0);
				font-size: 18px;
				margin: 0 14px 0 14px;
				font-weight: 700;
			}

			.edit-pen__icon {
				width: 30px;
				height: 24px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-6);
			}

			.input__group-name {
				width: 100%;
				box-sizing: border-box;
				padding: 20px 16px 20px 16px;
				background-color: var(--palette-live-bg-4);
				font-size: 18px;
				border: solid var(--palette-live-grey-9);
				outline: none;
				border-top-width: 1px;
				border-bottom-width: 1px;
				border-left-width: 0;
				border-right-width: 0;
			}

			.group-name__button-container {
				display: flex;
				margin: 20px 0 0 0;

				button:first-child{
					margin-right: 5px;
				}

				button:last-child{
					margin-left: 5px;
				}
			}
		}

		.arrow-down__icon {
			width: 20px;
			height: 20px;
			flex-shrink: 0;
			fill: var(--palette-live-grey-0);
		}

		.arrow--up {
			transform: rotate(180deg);
		}

		.arrow--down {
			transform: rotate(0deg);
		}

		.delete-icon {
			width: 24px;
			height: 24px;
			fill: var(--palette-live-grey-6);
			cursor: pointer;
		}
	}

	.stage-group__list-container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 14px 0 14px 0;
		border: solid var(--palette-live-grey-6);
		border-top-width: 0;
		border-bottom-width: 1px;
		border-left-width: 0;
		border-right-width: 0;

		&:last-child {
			border-bottom-width: 0;
		}

		.stage-group__list-block {
			display: flex;

			.stage-group__list-index {
				color: var(--palette-live-grey-7);
				font-size: 18px;
				margin: 0 20px 0 0;
				font-weight: 700;
			}

			.stage-group__list-name {
				color: var(--palette-live-grey-0);
				font-size: 18px;
				margin: 0 0 0 0;
				font-weight: 400;
			}
		}

		.drag-icon {
			width: 24px;
			height: 24px;
			flex-shrink: 0;
			fill: var(--palette-live-grey-6);
		}
	}

	.type-group__wrapper {
		padding: 5px 0 5px 0;
		display: flex;
		justify-content: center;
		border: solid var(--palette-live-grey-6);
		border-top-width: 0;
		border-bottom-width: 1px;
		border-left-width: 0;
		border-right-width: 0;

		.type-group__list {
			display: flex;
			align-items: center;
			justify-content: center;
			flex: 1;

			.struc-new__icon {
				width: 25px;
				height: 25px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}
	
			.round-robin__icon {
				width: 25px;
				height: 25px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}

			.type-group__name {
				color: var(--palette-live-grey-0);
				font-size: 18px;
				margin: 0 0 0 14px;
				font-weight: 700;
			}
		}

		.arrow-down__icon {
			width: 20px;
			height: 20px;
			flex-shrink: 0;
			fill: var(--palette-live-grey-0);
		}

		.arrow--up {
			transform: rotate(180deg);
		}

		.arrow--down {
			transform: rotate(0deg);
		}
	}

	.type-group__counter-wrapper {
		display: flex;
		align-items: center;
		padding: 16px 0 16px 0;

		.counter-groupe__name {
			color: var(--palette-live-grey-0);
			font-size: 18px;
			margin: 0 0 0 0;
			font-weight: 700;
		}

		.counter-groupe__container {
			display: flex;
			border: 1px solid var(--palette-live-grey-7);
			padding: 15px 22px 15px 22px;
			border-radius: 100px;
			align-items: center;

			.counter-groupe__counter-button {
				padding: 0 0 0 0;
				margin: 0 0 0 0;
				border: 0;
				border-radius: 0;
				background-color: var(--palette-live-bg-0-white);
				cursor: pointer;

				.remove-icon {
					width: 24px;
					height: 24px;
					fill: var(--palette-live-grey-7);
					flex-shrink: 0;
				}
			}

			.counter-groupe__counter {
				margin: 0 30px 0 30px;
				color: var(--palette-live-grey-0);
				font-size: 20px;
				font-weight: 500;
			}

			.counter-groupe__counter-button {
				padding: 0 0 0 0;
				margin: 0 0 0 0;
				border: 0;
				border-radius: 0;
				background-color: var(--palette-live-bg-0-white);
				cursor: pointer;

				.plus-white__icon {
					width: 19px;
					height: 19px;
					fill: var(--palette-live-grey-7);
					flex-shrink: 0;
				}
			}
		}
	}
}

.stage-group__popup-participants-item {
	margin: 0 16px 0 16px;
	border: solid var(--palette-live-grey-9);
	border-top-width: 1px;
	border-bottom-width: 1px;
	border-left-width: 0;
	border-right-width: 0;
	padding: 20px 0 20px 0;

	&--selected {
		background-color: #ebf4ff;
		cursor: pointer;
	}

	.stage-group__popup-participants-name {
		color: var(--palette-live-grey-0);
		font-size: 18px;
		margin: 0 0 0 0;
		font-weight: 400;
	}

	.stage-group__popup-participants-doubles {
		display: flex;
		align-items: center;

		.stage-group__popup-doubles-rating {
			color: var(--palette-live-grey-0);
			font-size: 18px;
			margin: 0 16px 0 0;
			font-weight: 400;
		}

		.stage-group__popup-doubles-name {
			color: var(--palette-live-grey-0);
			font-size: 18px;
			margin: 0 0 0 0;
			font-weight: 400;
		}

		.stage-group__popup-participants-name--first {
			color: var(--palette-live-grey-0);
			font-size: 18px;
			margin: 0 0 10px 0;
			font-weight: 400;
		}

		.stage-group__popup-participants-name--second {
			color: var(--palette-live-grey-0);
			font-size: 18px;
			margin: 0 0 0 0;
			font-weight: 400;
		}
	}
}