.group-player__double-container {
	display: flex;
	justify-content: space-between;
	padding: 15px 0 15px 0;
	align-items: center;

	.group-player__double {
		display: flex;
		flex: 1 1;
		flex-wrap: wrap;
		align-items: center;

		.group-player__double-index {
			margin: 0 20px 0 0;
			font-size: 18px;
			color: var(--palette-live-grey-7);
			font-weight: 700;
		}

		.group-player__double-noplayer {
			margin: 0 0 10px 0;
			font-size: 18px;
			color: var(--palette-live-grey-0);
			font-weight: 400;

			&:last-child {
				margin: 0 0 0 0;
			}
		}

		.group-player__double-name {
			margin: 0 0 10px 0;
			font-size: 18px;
			color: var(--palette-live-grey-0);
			font-weight: 400;

			&:last-child {
				margin: 0 0 0 0;
			}
		}

		.group-player__double-rating {
			margin: 0 0 0 10px;
			font-size: 18px;
			color: var(--palette-live-grey-0);
			font-weight: 400;
		}
	}

	.group-player__buttons {

		.icon-deletion {
			width: 24px;
			height: 24px;
		}

		.drag-icon {
			width: 24px;
			height: 24px;
			fill: var(--palette-live-grey-6);
		}
	}
}

