.header_tournament__wrapper{
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 15px;

	.header_tournament__title {
		color: var(--palette-live-grey-0);
		font-size: 22px;
		margin: 0 0 10px 0;
		font-weight: 600;
	}

	.header_tournament__container {
		width: 100%;
		display: flex;
		flex-direction: column;

		.header_tournament__date {
			color: var(--palette-live-grey-0);
			font-size: 16px;
			margin: 0 0 5px 0;
			font-weight: 500;
		}

		.header_tournament__name {
			color: var(--palette-live-grey-0);
			font-size: 16px;
			margin: 0 0 0 0;
			font-weight: 500;
		}
	}
}