.pre-stage {
	background-color: var(--palette-live-grey-8);
	margin: 0 0 100px 0;
	display: flex;
	flex-direction: column;

	.pre-stage__wrapper {
		background-color: var(--palette-live-bg-1-white);
		padding: 25px 16px 0 16px;
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 0;
		flex: 1;

		.pre-stage__header {
			display: flex;
			align-items: center;
			border: solid var(--palette-live-grey-7);
			border-top-width: 0;
			border-bottom-width: 1px;
			border-left-width: 0;
			border-right-width: 0;
			padding: 0 0 16px 0;

			.arrow-back__icon {
				width: 24px;
				height: 24px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}

			.pre-stage__header-container {
				display: flex;
				flex-direction: column;
				align-items: center;
				flex: 1;

				.pre-stage__stage {
					color: var(--palette-live-grey-0);
					font-size: 18px;
					margin: 0 0 10px 0;
					font-weight: 700;
				}

				.pre-stage__description {
					color: var(--palette-live-grey-0);
					font-size: 18px;
					margin: 0 0 0 0;
					font-weight: 700;
				}
			}
		}

		.pre-stage__list {


			.pre-stage__block-wrapper {
				padding: 16px 0 16px 0;
				border: solid var(--palette-live-grey-12);
				border-top-width: 0;
				border-bottom-width: 1px;
				border-left-width: 0;
				border-right-width: 0;
				display: flex;
				justify-content: space-between;
				align-items: center;

				.pre-stage__block-container {
					display: flex;

					.pre-stage__block-counter {
						color: var(--palette-live-grey-0);
						font-size: 18px;
						margin: 0 10px 0 0;
						font-weight: 400;
					}

					.pre-stage__block-name {
						color: var(--palette-live-grey-0);
						font-size: 18px;
						margin: 0 0 0 0;
						font-weight: 400;
					}
				}

				.DND-icon {
					width: 24px;
					height: 24px;
					flex-shrink: 0;
					fill: var();
				}
			}
		}
	}
}