.tournament__participants-section {
	padding: 0 15px 0 15px;
}

.tournament__participants-title {
	font-size: 20px;
	font-weight: 500;
	margin: 10px 0;
}

.nocontent__link{
	font-weight: 500;
	color: var(--palette-live-blue);
}

.popup__input-player--first,
.popup__input-player--second {
	margin-bottom: 15px;
}

.popup-button__addition-doubles {
	margin-bottom: 10px;
	display: flex;
	justify-content: center;
}

.popup-search__list-item {
	font-size: 20px;
	padding: 15px 0;
	border-bottom: 1px solid #CCCCCC;
	display: flex;
	justify-content: space-between;
	align-items: center;
	cursor: pointer;
}

.popup-search__list-item:nth-child(3) {
	border-top: 1px solid #CCCCCC;
}

.popup-search__player-name{
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	display: flex;
	align-items: center;
	min-width: 100px;
	margin: 0 0 0 5px;
}
.popup-search__player-name > span {
	font-size: 15px;
	margin-right: 5px;
}

.popup__limit-title {
	color: #707070;
	font-size: 16px;
	margin: 15px 0;
}

.popup-search__list-item-status{
	font-size: 14px;
	color: #4694D9;
	margin:  0 5px 0 10px;
}

.participants__button-addition {
	margin-top: 20px;
	display: flex;
	justify-content: center;
}

.participants {
	margin: 20px 0 40px 0;
	border: 1px solid #CCCCCC;
	border-radius: 11px;
	overflow: hidden;

	.icon-deletion{
		width: 24px;
		height: 24px;
		padding: 4px;
		background-color: rgb(255, 226, 226);
		border-radius: 100px;
	}
}

.participants--singles{
	overflow: hidden;
}

.participants-item {
	display: flex;
	align-items: center;
	border-bottom: 1px solid #CCCCCC;
	padding: 0 15px;
	font-size: 20px;
}

.participants-item:last-child {
	border-bottom: 0px solid #CCCCCC;
}

.participants-item--double {
	display: flex;
	align-items: center;
	justify-content: space-between;
	border-bottom: 1px solid #CCCCCC;
	padding: 15px 15px;
	font-size: 20px;
}

.participants-item--double:last-child {
	border-bottom: 0px solid #CCCCCC;
}

.participants__number {
	width: 35px;
	height: 35px;
	border-radius: 100%;
	background-color: #EBF4FF;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 15px 0 0;
	font-size: 18px;
}

.participants__names {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 100%;
	font-size: 20px;
	padding: 5px 0;
	overflow: hidden;
}

.participants__name {
	margin: 15px 4px 15px 0;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.participants__palyer--first,
.participants__palyer--second {
	margin: 0;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.participants__palyer--first {
	margin-bottom: 10px;
}

.participants-icons {
	display: flex;
	justify-content: center;
	align-items: center;
}

.participants__icon-edit {
	display: block;
	margin-bottom: 10px;
	cursor: pointer;
}

.participants__button-save{
	box-sizing: border-box;
	width: calc(100vw - 15px);
	position: fixed;
	padding: 0 8px;
	bottom: 80px;
	left: 0px;
	right: 0px;
	margin: auto;
	display: flex;
	z-index: 9999;
	justify-content: center;
	pointer-events: none;

	button {
		pointer-events: all;
	}
}

.participants__popup-container {
	margin: 0 20px 0 20px;
}