.stage-creation {
	background-color: var(--palette-live-grey-8);
	margin: 0 0 100px 0;
	display: flex;
	flex-direction: column;

	.stage-creation__wrapper {
		background-color: var(--palette-live-bg-1-white);
		padding: 16px 16px 0 16px;
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 0;
		flex: 1;
	
		.stage-creation__header {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 0 16px 16px 16px;
			border: solid var(--palette-live-grey-7);
			border-top-width: 0;
			border-right-width: 0;
			border-bottom-width: 1px;
			border-left-width: 0;
	
			.stage-creation__quantity {
				margin: 0 0 0 0;
				color: var(--palette-live-grey-0);
				font-size: 20px;
				font-weight: 500;
			}
	
			.stage-creation__stage-number {
				margin: 0 0 0 0;
				color: var(--palette-live-grey-0);
				font-size: 22px;
				font-weight: 600;
				display: flex;
				align-items: center;

				.stage-creation__stage-button {
					border: 0;
					background-color: var(--palette-live-bg-1-white);
					padding: 0 0 0 0;
					margin: 0 0 0 20px;
					display: flex;
					cursor: pointer;

					.edit-note__icon {
						width: 24px;
						height: 24px;
						flex-shrink: 0;
						fill: var(--palette-live-grey-6);
					}
				}
			}
	
			.arrow-down__icon {
				width: 20px;
				height: 20px;
				flex-shrink: 0;
				cursor: pointer;
			}
		}

		.stage-creation__list-participants {
			
		}

		.stage-creation__radio-container {
			display: flex;
			justify-content: space-between;
			padding: 10px 0 10px 0;
			border: solid var(--palette-live-grey-7);
			border-top-width: 0;
			border-right-width: 0;
			border-bottom-width: 1px;
			border-left-width: 0;

			.stage-creation__radio-element {
				width: 100%;
				display: flex;
				justify-content: center;
				align-items: center;
				padding: 10px 0 10px 0;
				flex-direction: row-reverse;

				&:first-child {
					border: solid var(--palette-live-grey-7);
					border-top-width: 0;
					border-right-width: 1px;
					border-bottom-width: 0;
					border-left-width: 0;
				}

				input[type=radio] {
					display: none;
					cursor: pointer;
				}
			
				label {
					display: flex;
					align-items: center;
				}
			
				label:before {
					content: "";
					display: inline-block;
					width: 20px;
					height: 20px;
					border: 2px solid var(--palette-live-blue);
					border-radius: 100%;
					margin: 0 15px 0 0;
					flex-shrink: 0;
				}
			
				input[type=radio]:checked+label:before {
					background-color: #0085F4;
					box-shadow: inset 0px 0px 0px 2px rgb(255 255 255);
				}

				.stage-creation__radio-lable {
					color: var(--palette-live-grey-0);
					font-size: 18px;
					margin: 0 15px 0 0;
					display: block;
					font-weight: 700;
					display: flex;
					cursor: pointer;
				}
			}
		}

		.stage-creation__counter-groupe {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 30px 0 0 0;

			.counter-groupe__name {
				color: var(--palette-live-grey-0);
				font-size: 18px;
				margin: 0 0 0 0;
				font-weight: 700;
			}

			.counter-groupe__container {
				display: flex;
				border: 1px solid var(--palette-live-grey-7);
				padding: 15px 22px 15px 22px;
				border-radius: 100px;
				align-items: center;

				.counter-groupe__counter {
					margin: 0 30px 0 30px;
					color: var(--palette-live-grey-0);
					font-size: 20px;
					font-weight: 500;
				}

				.counter-groupe__counter-button {
					padding: 0 0 0 0;
					margin: 0 0 0 0;
					border: 0;
					border-radius: 0;
					background-color: var(--palette-live-bg-0-white);
					cursor: pointer;

					.remove-icon {
						width: 24px;
						height: 24px;
						fill: var(--palette-live-grey-7);
						flex-shrink: 0;
					}

					.plus-white__icon {
						width: 19px;
						height: 19px;
						fill: var(--palette-live-grey-7);
						flex-shrink: 0;
					}
				}
			}
		}

		.stage-creation__button-container {
			margin: 30px 0 0 0;
			display: flex;
			align-items: center;
			flex-direction: column;

			.stage-creation__delete-button {
				width: 100%;
				color: var(--palette-live-grey-7);
				font-size: 20px;
				margin: 20px 0 0 0;
				padding: 0 0 0 0;
				border: 0;
				font-weight: 500;
				background-color: var(--palette-live-bg-1-white);
				cursor: pointer;
			}
		}
	}

	.stage-form__confirmation {
		color: var(--palette-live-grey-0);
		font-size: 15px;
		text-align: center;
	}

	.stage-form__confirmation-buttons {
		display: flex;
		justify-content: space-between;

		button:first-child{
			margin-right: 5px;
		}

		button:last-child{
			margin-left: 5px;
		}
	}
}