.login__container {
	height: 100dvh;
	width: 100vw;
	display: flex;
	flex-direction: column;
	align-items: center;

	.login__back-button-container {
		width: 100%;
		box-sizing: border-box;
		padding: 20px 20px 0 20px;
	}

	.login__logo-container {
		display: flex;
		justify-content: center;

		.login__logo-icon {
			width: 49px;
			height: 49px;
			fill: var(--palette-live-blue);
		}
	}

	.login__form-container {
		display: flex;
		flex-direction: column;
		padding: 40px 20px 20px 20px;
		box-sizing: border-box;

		.login__form-title {
			color: var(--palette-live-grey-1);
			font-size: 32px;
			margin: 0 0 4px 0;
			padding: 0 0 0 0;
			font-weight: 700;
		}

		.login__form-description {
			color: var(--palette-live-grey-3);
			font-size: 18px;
			margin: 0 0 30px 0;
			padding: 0 0 0 0;
		}

		.login__form-wrapper {
			display: flex;
			flex-direction: column;

			.button {
				margin: 9px 0 0 0;
			}
		}

		.login__reset-password {
			color: var(--palette-live-blue);
			font-size: 18px;
			margin: 15px 0 0 0;
			text-decoration: none;
			text-align: right;
		}
	}
}