.stage-form {
	background-color: var(--palette-live-grey-8);
	margin: 0 0 140px 0;
	display: flex;
	flex-direction: column;

	.stage-form__wrapper {
		background-color: var(--palette-live-bg-1-white);
		padding: 16px 16px 0 16px;
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 0;
		flex: 1;
		position: relative;

		.stage-form__header {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 0 16px 16px 16px;
			border: solid var(--palette-live-grey-7);
			border-top-width: 0;
			border-right-width: 0;
			border-bottom-width: 1px;
			border-left-width: 0;

			.stage-form__quantity {
				margin: 0 0 0 0;
				color: var(--palette-live-grey-0);
				font-size: 20px;
				font-weight: 500;
			}

			.stage-form__stage-number {
				margin: 0 0 0 0;
				color: var(--palette-live-grey-0);
				font-size: 22px;
				font-weight: 600;
				display: flex;
				align-items: center;

				.stage-creation__stage-button {
					background-color: transparent;
					border: 0;
					padding: 0 0 0 0;
					margin: 0 0 0 10px;
					cursor: pointer;
					display: flex;

					.edit-note__icon {
						width: 34px;
						height: 34px;
						flex-shrink: 0;
						fill: var(--palette-live-grey-6);
					}
				}
			}

			.arrow-down__icon {
				width: 20px;
				height: 20px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}

			.arrow--up {
				transform: rotate(180deg);
			}

			.arrow--down {
				transform: rotate(0deg);
			}
		}

		.stage-form__list-participants {
			margin: 0 0 20px 0;

			.stage-form__participants-list-wrapper{
				.stage-form__participants-list-container {
					display: flex;
					align-items: center;
					padding: 16px 16px 16px 16px;

					.stage-form__participants-list-index {
						font-size: 18px;
						margin: 0 16px 0 0;
						font-weight: 400;
					}

					.stage-form__participants-list-name {
						font-size: 18px;
						margin: 0 0 10px 0;
						font-weight: 400;

						&:last-child {
							margin: 0 0 0 0;
						}
					}

					.stage-form__participants-list-rating {
						font-size: 18px;
						margin: 0 0 0 16px;
						font-weight: 400;
					}
				}
			}
		}

		.stage-form__level {
			.stage-form__level-wrapper {
				display: flex;
				justify-content: space-between;
				margin: 20px 0 20px 0;

				.stage-form__level-container {
					display: flex;

					.stage-form__level-name {
						color: var(--palette-live-grey-0);
						font-size: 24px;
						margin: 0 15px 0 0;
						font-weight: 600;
					}
				
					.edit-pen__icon {
						width: 24px;
						height: 24px;
						flex-shrink: 0;
						fill: var(--palette-live-grey-6);
					}
				}

				.delete-icon {
					width: 24px;
					height: 24px;
					fill: var(--palette-live-grey-6);
					cursor: pointer;
				}
			}
		
			.stage-form__group-container {
				.stage-form__group {
					margin: 0 0 20px 0;
				}
			}
		}

		.stage-form__button-bottom{
			width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: space-between;
		}

		.stage-form__delete-button {
			width: 100%;
			color: var(--palette-live-grey-7);
			font-size: 20px;
			margin: 20px 0 0 0;
			padding: 0 0 0 0;
			border: 0;
			font-weight: 500;
			background-color: var(--palette-live-bg-1-white);
			cursor: pointer;
		}

		.stage-form__confirmation {
			color: var(--palette-live-grey-0);
			font-size: 15px;
			margin: 0 16px 20px 16px;
			text-align: center;
		}

		.stage-form__confirmation-buttons {
			display: flex;
			justify-content: space-between;

			button:first-child{
				margin-right: 5px;
			}

			button:last-child{
				margin-left: 5px;
			}
		}

		.stage-form__no-group {
			display: flex;
			padding: 13px;
			border: 1.5px dashed var(--palette-live-red-1);
			border-radius: 10px;

			.stage-form__no-group--title {
				color: var(--palette-live-grey-0);
				font-size: 18px;
				margin: 0 0 0 0;
				text-align: center;
			}
		}
	}
}