.popup__wrapper {
	width: 100%;
	display: flex;
	background-color: rgba(0, 0, 0, 0.29);
	position: fixed;
	top: 0;
	left: 0;
	align-items: center;
	justify-content: center;
	z-index: 9999;

	height: 100dvh;

	.popup__container {
		width: 100%;
		background-color: #fff;
		border-radius: 11px;
		padding: 20px 0;
		margin: 20px;

		overflow: auto;
		max-height: -webkit-fill-available;

		.popup__header {
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			padding: 0 15px;
		}

		.popup__title {
			font-size: 20px;
			margin: 0;
			font-weight: 500;
			text-align: center;
		}

		.popup__close-icon {
			cursor: pointer;
			fill: var(--palette-live-grey-0);
		}

		.popup__body {
			position: relative;
			overflow: auto;
			padding: 0 15px;
		}

		.popup__body::-webkit-scrollbar {
			width: 0;
		}

		.react-datepicker-wrapper:first-child {
			margin-bottom: 15px;
		}
	}
}