.stage-plug {
	flex: 1;
	display: flex;
	flex-direction: column;

	.stage-plug__wrapper{
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: auto 0;
	
		.stage-plug__container{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
	
			.stage-plug__title{
				display: block;
				margin: 30px 0 20px 0;
				font-size: 24px;
				font-weight: 600;
				color: var(--palette-live-grey-0);
			}
	
			.stage-plug__subtitle{
				font-size: 18px;
				font-weight: 400;
				color: var(--palette-live-grey-0);
			}
		}
	}
}