	.registration__container {
		height: 100dvh;
		overflow: auto;

	.registration__back-button-container {
		margin: 20px 20px 0 20px;
	}

	.registration__logo-container {
		display: flex;
		justify-content: center;

		.registration__logo-icon {
			width: 49px;
			height: 49px;
			fill: var(--palette-live-blue);
		}
	}

	.registration__form-container {
		display: flex;
		flex-direction: column;
		margin: 40px 20px 10px 20px;

		.registration__form-title {
			color: var(--palette-live-grey-1);
			font-size: 32px;
			margin: 0 0 4px 0;
			padding: 0 0 0 0;
			font-weight: 700;
		}

		.registration__form-description {
			color: var(--palette-live-grey-3);
			font-size: 18px;
			margin: 0 0 30px 0;
			padding: 0 0 0 0;
		}

		.registration__form-wrapper {
			display: flex;
			flex-direction: column;

				.button {
					margin: 0 0 25px 0;
				}
			}
		}
	}