.radio-button__container {
	padding: 16px;
	border-bottom: 1px solid var(--palette-live-grey-5);
	cursor: pointer;

	input[type=radio] {
		display: none;
		cursor: pointer;
	}

	label {
		display: flex;
		align-items: center;
		cursor: pointer;
	}

	label:before {
		content: "";
		display: inline-block;
		width: 20px;
		height: 20px;
		border: 2px solid var(--palette-live-blue);
		border-radius: 100%;
		margin: 0 15px 0 0;
		flex-shrink: 0;
	}

	input[type=radio]:checked+label:before {
		background-color: #0085F4;
		box-shadow: inset 0px 0px 0px 2px rgb(255 255 255);
	}
}

.radio-button__container:last-of-type {
	border-bottom: none;
}